import Scroller from "vux/src/components/scroller/index";
import http from "@/axios"

export default {
    components: {Scroller},
    name:"join_activity_member_list",
    props:['result'],
    data(){
        return{
            activityId:this.$route.query.id,
            memberList:[],
            hasMore:true,
            pageNo:1,
            scrollerStatus: {
                pullupStatus: 'default'
            },
            upobj: {
                content: '请上拉刷新数据',
                pullUpHeight: 60,
                height: 40,
                autoRefresh: false,
                downContent: '请上拉刷新数据',
                upContent: '请上拉刷新数据',
                loadingContent: '<span style="color: red">加载中...</span>',
                clsPrefix: 'xs-plugin-pullup-'
            },
        }
    },
    methods:{
        getArticleList(){
            var current = (this.pageNo - 1) * 10;
            var self = this;
            http.getMoneyUserList({
                id:this.activityId,
                currentResult:current,
                pageSize:10
            },function (response) {
                if(response.total > (current + 10)){
                    self.scrollerStatus.pullupStatus = 'default'
                    self.hasMore = true;
                }else{
                    self.scrollerStatus.pullupStatus = 'disabled' // 禁用下拉
                    self.hasMore = false;
                }

                if(self.pageNo == 1){
                    self.memberList = [];
                }

                self.memberList = self.memberList.concat(response.data);
                self.$nextTick(function () {
                    self.$refs.scroller.donePullup();
                    self.$refs.scroller.donePulldown()
                })
            })
        }
    },
    mounted(){
        this.getArticleList();
    }
}



